.catalog {
  padding-bottom: 90px;

  &__title {
    font-weight: 700;
    font-size: 42px;
    line-height: 48px;
    text-align: center;
    color: var(--primary-color);
    text-align: center;
    margin: 0;
    margin-bottom: 34px;
  }
}

.catalog-card {

  &__title {
    font-weight: 700;
    font-size: 28px;
    line-height: 32px;
    text-align: center;
    color: var(--title-color);
    margin: 0;
    margin-bottom: 51px;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      height: 5px;
      background-image: linear-gradient(90deg, #2D3A94, #51ABE3);
      z-index: -1;
    }
  }

  &__title-decore {
    background-color: #fff;
    padding: 0 58px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 36px;
  }

  &__item {
    grid-column: 4 span;
    background: var(--light-color);
    border: 2px solid #243469;
    box-shadow: 4px 4px 40px rgba(36, 52, 105, 0.05);
    border-radius: 20px;
    padding: 40px 45px 65px;
  }

  &__item-title {
    font-weight: 700;
    font-size: 24px;
    line-height: 34px;
    color: var(--title-color);
    margin: 0;
    margin-bottom: 40px;
  }

  &__item-image {
    margin-bottom: 15px;
  }

  &__item-descr {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__item-sum {
    font-weight: 500;
    font-size: 32px;
    line-height: 37px;
    color: var(--primary-color);
  }

  &__item-info {
    width: 87px;
    height: 60px;
    position: relative;
    background: #fff;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    border: 4px solid transparent;
    background: linear-gradient(white, white) padding-box,
      linear-gradient(118.92deg, #F03A2B -8.89%, #FCC009 189.86%) border-box;

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 14px;
      height: 38px;
      background-image: url(../img/info.svg);
      background-size: cover;
      background-position: 0 0;
      background-repeat: no-repeat;
    }
  }

  &__item-plus {
    width: 87px;
    height: 60px;
    position: relative;
    background-image: linear-gradient(118.92deg, #F03A2B -8.89%, #FCC009 189.86%);
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.07);
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    margin-left: 10px;

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 40px;
      height: 40px;
      background-image: url(../img/plus.svg);
      background-size: cover;
      background-position: 0 0;
      background-repeat: no-repeat;
    }
  }

  &__btn {
    grid-column: 12 span;
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    color: var(--title-color);
    margin-bottom: 55px;
  }

  &__save {
    grid-column: 12 span;
    font-weight: 700;
    font-size: 28px;
    line-height: 32px;
    text-align: center;
    color: var(--light-color);
    display: block;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    background-color: var(--title-color);
    padding: 20px 0;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      background: #F03A2B;
      filter: blur(300px);
    }
  }
}

.graph-modal {

  &__container {}

  &__close {}

  &__content {}
}
