.cert {

  &__slider {
    margin-left: -64px;
    margin-right: 20px;
    padding-top: 60px;
    padding-left: 50px;
  }

  &__slider-btn {
    @include bg-cover;

    width: 87px;
    height: 60px;
    position: absolute;
    top: 53.5%;
    z-index: 10;

    &--prev {
      background-image: url(../img/arr-left.svg);
      left: 0;
    }

    &--next {
      background-image: url(../img/arr-right.svg);
      right: 0;
    }
  }

  &__slide {
    position: relative;
    padding-top: 70px;

    &::before {
      content: "";
      position: absolute;
      top: 30px;
      left: 39px;
      max-width: 333px;
      max-height: 452px;
      width: 104%;
      height: 100%;
      background-image: url(../img/line-top-small.svg);
      background-size: cover;
      background-position: 0 0;
      background-repeat: no-repeat;
      z-index: -1;
    }

    // &::after {
    //   content: "";
    //   position: absolute;
    //   top: 13%;
    //   right: 10.5%;
