.benefit {
  position: relative;
  padding-bottom: 120px;

  &::before {
    content: "";
    position: absolute;
    top: -25px;
    left: 10.5%;
    max-width: 100%;
    max-height: 768px;
    width: 101%;
    height: 100%;
    background-image: url(../img/line-top.svg);
    background-size: cover;
    background-position: 0 0;
    background-repeat: no-repeat;
    z-index: -1;
  }

  &::after {
    content: "";
    position: absolute;
    top: 24px;
    right: 10.5%;
    max-height: 768px;
    width: 105.5%;
    height: 100%;
    background-image: url(../img/line-bottom.svg);
    background-size: cover;
    background-position: 0 0;
    background-repeat: no-repeat;
    z-index: -2;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12,1fr);
    gap: 36px;
  }

  &__item {
    background-color: var(--title-color);
    padding: 55px 45px 35px;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    grid-column: 4 span;
    min-height: 365px;
    position: relative;
  }

  &__image {
    position: absolute;
    top: 35px;
    left: 45px;
  }

  &__item-title {
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 35px;
    text-align: center;
    margin-left: 55px;
  }

  &__item-text {
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    color: var(--light-color);
    margin: 0;
  }
}
