.innovate {
  position: relative;
  padding-bottom: 150px;

  &::before {
    content: "";
    position: absolute;
    top: 66px;
    left: 10.5%;
    max-width: 100%;
    min-height: 876px;
    width: 101%;
    background-image: url(../img/line-top.svg);
    background-size: cover;
    background-position: 0 0;
    background-repeat: no-repeat;
    z-index: -1;
  }

  &::after {
    content: "";
    position: absolute;
    top: 13%;
    right: 10.5%;
    max-height: 850px;
    width: 117%;
    height: 100%;
    background-image: url(../img/line-bottom.svg);
    background-size: cover;
    background-position: 0 0;
    background-repeat: no-repeat;
    z-index: -2;
  }

  &__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 37px;
    text-align: center;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 55px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 55px 36px;
  }

  &__item {
    grid-column: 4 span;
  }

  &__item-title {
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    color: var(--primary-color);
    text-align: center;
    margin: 0;

  }
}

.video {
  position: relative;
  width: 100%;
  height: 300px;
  overflow: hidden;
  margin-bottom: 20px;
  border-top-left-radius: 20px;
  border-bottom-right-radius: 20px;

  &--visible {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(42, 41, 40, 0.4);
      z-index: 1;
    }
  }

  & video {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__play {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 125px;
    height: 90px;
    transform: translate(-50%, -50%);
    color: var(--color-light);
    transition: color 0.3s ease-in-out;
    z-index: 2;

    &:hover {
      opacity: 0.7;
    }

    &--hidden {
      display: none;
    }
  }
}
