.compare {
  padding-bottom: 174px;

  &__container {
    background-image: url(../img/ogn.png);
    background-size: 597px 698px;
    background-position: center 95px;
    background-repeat: no-repeat;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    position: relative;
    padding-bottom: 35px;
  }

  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 37px;
    text-align: center;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 39px;
  }

  &__left {
    & .compare__item {
      &::before {
        content: '';
        position: absolute;
        bottom: -22px;
        left: 0;
        width: 150%;
        height: 3px;
        background: linear-gradient(90deg, #2D3A94, #51ABE3);
        z-index: -1;
        opacity: 0.5;
      }
    }
  }

  &__list {}

  &__list-title {
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    text-transform: uppercase;
    color: var(--title-color);
    margin: 0;
    margin-bottom: 42px;
  }

  &__item {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: var(--primary-color);
    position: relative;

    &:not(:last-child) {
      margin-bottom: 52px;
    }
  }

  &__item--bold {
    font-weight: 700;
    font-size: 24px;
    padding-left: 20px;
  }

  &__right {
    text-align: right;

    & .compare__item {
      &::before {
        content: '';
        position: absolute;
        bottom: -22px;
        right: 0;
        width: 150%;
        height: 3px;
        background: linear-gradient(90deg, #FCC009, #F14624);
        z-index: -1;
        opacity: 0.5;
      }
    }
  }
}
