.hero {
  padding-top: 52px;
  padding-bottom: 105px;

  &__container {
    background-image: url(../img/hero-bg.png);
    background-size: cover;
    background-position: 0 0;
    background-repeat: no-repeat;
    position: relative;
    height: 560px;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #243469;
      opacity: 0.8;
      border-top-left-radius: 20px;
      border-bottom-right-radius: 20px;
    }

    &::after {
      content: '';
      position: absolute;
      bottom: 15px;
      right: 50px;
      max-width: 190px;
      max-height: 190px;
      width: 100%;
      height: 100%;
      background-color: #F03A2B;
      filter: blur(100px);
    }
  }

  &__wrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;

    &::before {
      content: "";
      position: absolute;
      top: -22px;
      left: -24px;
      max-width: 1480px;
      max-height: 561px;
      width: 101%;
      height: 100%;
      background-image: url(../img/line-top.svg);
      background-size: cover;
      background-position: 0 0;
      background-repeat: no-repeat;
      z-index: -1;
    }

    &::after {
      content: "";
      position: absolute;
      bottom: -22px;
      right: -24px;
      max-width: 1480px;
      max-height: 561px;
      width: 101%;
      height: 100%;
      background-image: url(../img/line-bottom.svg);
      background-size: cover;
      background-position: 0 0;
      background-repeat: no-repeat;
      z-index: -1;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 52px;
    line-height: 70px;
    text-transform: uppercase;
    color: var(--light-color);
    position: relative;
    top: 36%;
    max-width: 900px;
    left: 100px;
    z-index: 1;
    margin: 0;
  }

  &__btn {
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    color: var(--light-color);
    text-transform: lowercase;
    position: absolute;
    right: 16%;
    bottom: 87px;

    &::before {
      content: '';
      position: absolute;
      top: -15px;
      right: -54%;
      width: 87px;
      height: 60px;
      background-image: linear-gradient(118.92deg, #F03A2B -8.89%, #FCC009 189.86%);
      box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.07);
      border-top-left-radius: 20px;
      border-bottom-right-radius: 20px;
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: -43%;
      width: 30px;
      height: 30px;
      background-image: url(../img/save.svg);
      background-size: cover;
      background-position: 0 0;
      background-repeat: no-repeat;
    }
  }
}
