.header {
  position: absolute;
  top: 65px;
  left: 0;
  width: 100%;
  z-index: 1;

  &__container {
    display: flex;
    justify-content: space-between;
    align-content: center;
  }

  &__contacts {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex: 2 1 auto;
  }

  &__mail {
    font-weight: 400;
    font-size: 15px;
    line-height: 17px;
    color: var(--light-color);
  }

  &__tel {
    font-weight: 700;
    font-size: 15px;
    line-height: 17px;
    text-transform: uppercase;
    color: var(--light-color);
  }

  &__social {
    display: flex;
    align-items: flex-start;

  }

  &__social-item {
    width: 19px;
    height: 19px;
    background-color: red;

    &:not(:last-child) {
      margin-right: 7px;
    }

  }

  &__social-link {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 100%;
  }

  &__social-link--tg {
    background-image: url(./img/telegramm.svg);
  }

  &__social-link--whatsapp {}

  &__social-link--viber {}
}

.logo {

  &__image {}
}
