.about {
  padding-top: 50px;

  &__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 37px;
    text-align: center;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 30px;
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    color: var(--primary-color);
    margin: 0;
    margin-bottom: 30px;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    margin-top: 90px;
  }

  &__item {
    max-width: 335px;
    display: flex;
    align-items: center;
  }

  &__name {
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: var(--primary-color);
    padding-left: 40px;
    margin: 0;
  }

}
