.contacts {

  &__container {
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    background-color: var(--title-color);
  }

  &__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 37px;
    text-align: center;
    color: var(--light-color);
  }

  &__wrapper {

  }

  &__col {}

  &__col-title {
    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    color: var(--light-color);

  }

  &__col-text {
    font-weight: 400;
    font-size: 18px;
    line-height: 32px;
    color: var(--light-color);
    font-style: normal;
  }
}
